<script>
import VueScriptComponent from 'vue-script-component'
import {
    mapState,
    mapActions
} from "vuex"
export default {
    name: "Header",
    data() {
        return {}
    },
    components: {
    VueScriptComponent
     },
    
    computed: {
        ...mapState({
            auth: state => state.Auth,
            idUser: state => state.Auth.user.id,
        })
    },
    mounted() {
        if (!this.idUser) {            
            this.loginRedirect()
        }
        //colocar a verficação de login em outro lugar 
          
    },
    methods: {
        ...mapActions([
            'logout'
        ]),
        loginRedirect() {
            if (!this.idUser) {

                this.$router.push({
                    name: 'login'
                })
            }
        }
    },
    watch: {
        idUser() {

            this.loginRedirect()
        }
    }
}
</script>
